@import "../../assets/fonts/fonts.scss";
@import "../../assets/scss/theme.scss";
@import "../../assets/scss/mixins";

.bg-home-banner-default {
  background-color: $x96-mirage;
  -webkit-transition: background-color 0.5s ease;
  -ms-transition: background-color 0.5s ease;
  transition: background-color 0.5s ease;
}
.bg-home-banner-white {
  background-color: white !important;
}
li {
  color: rgba(255, 255, 255, 0.75);
  font-size: 1rem;
  display: flex;
  margin: auto;
}
#homepage-section-1-main-wrapper {
  background-image: url(../../assets/images/homepage-section1-bg.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  .heading-wrapper {
    h1 {
      font-size: 6rem;
    }
  }
  .blurb-wrapper {
    p {
      font-size: 1rem;
    }
  }
  .mouse-icon-wrapper {
    .position-absolute {
      left: 49%;
      top: 1rem;
    }
  }
  #homepage-section-1-2-main-wrapper {
    .heading-wrapper {
      h2 {
        color: $x96-dark-purple;
        font-size: 10rem;
      }
    }
    .content-wrapper {
      -webkit-background-clip: unset;
      -webkit-text-fill-color: $x96-mirage;
      h3 {
        font-size: 3.75rem;
        width: 70%;
        color: $x96-mirage;
      }
      p {
        font-size: 1rem;
        color: $x96-mirage;
        width: 70%;
      }
    }
  }
}
#homepage-section-2-main-wrapper {
  .animated-heading {
    background-color: $x96-mirage;
    background-image: url(../../assets/images/bg-discover-heading.svg);
    background-attachment: fixed;
    background-position: center;
    background-size: 80%;
    background-repeat: no-repeat;
    font-size: 13rem;
    -webkit-background-clip: text;
    color: transparent;
    font-family: "Eina-Bold", Arial, sans-serif !important;
  }
  .heading-wrapper {
    h2 {
      color: #0a034e;
      font-size: 10rem;
    }
  }
  .content-wrapper {
    h3 {
      font-size: 3.75rem;
      width: 70%;
      color: #181b23;
    }
    p {
      font-size: 1rem;
      color: #181b23;
      width: 70%;
      text-align: left;
    }
  }
}
#homepage-section-3-main-wrapper {
  background-color: #181b23;
  .content-wrapper {
    h4 {
      font-size: 2rem;
    }
    p {
      color: rgba($color: white, $alpha: 0.75);
      font-size: 1rem;
    }
  }
  .services-cards-main-wrapper {
    -webkit-transition: all 0.6s ease-out;
    -moz-transition: all 0.6s ease-out;
    -ms-transition: all 0.6s ease-out;
    -o-transition: all 0.6s ease-out;
    transition: all 0.6s ease-out;
    .services-card-1 {
      background-color: $x96-orange;
      &:hover {
        .images-wrapper {
          .right-to-left {
            transform: scale(2) translateX(-9rem) translateY(-2rem) !important;
          }
          .bottom-up {
            transform: scale(1.2) translateX(-2.5rem) translateY(-1rem) !important;
          }
        }
      }
    }
    .services-card-2 {
      background: linear-gradient(132.99deg, #3fc1c9 12.01%, #1986a9 79.74%);
      .bottom-up {
        right: -9rem;
        bottom: -6rem;
      }
      .right-to-left {
        bottom: -3rem;
        right: -3rem;
      }
      &:hover {
        .images-wrapper {
          .bottom-up {
            transform: rotate(-43deg) scale(1.25) !important;
          }
          .right-to-left {
            transform: rotate(30deg) scale(1.2) !important;
          }
        }
      }
    }
    .services-card-3 {
      background: linear-gradient(127.54deg, #4692ff 5.15%, #0069ff 96.27%);
      .images-wrapper {
        .right-to-left {
          bottom: -1rem;
          right: -2.5rem;
        }
        .bottom-up {
          bottom: -1rem;
          right: -2rem;
        }
      }
      &:hover {
        .right-to-left {
          transform: rotate(30deg) scale(1.45) translateY(2rem) translateX(1rem) !important;
        }
        .bottom-up {
          transform: rotate(-30deg) scale(1.1) translateX(1rem) translateY(1rem) !important;
        }
      }
    }
    .services-card-4 {
      background: $x96-doc-pink;
      .images-wrapper {
        .bottom-up {
          bottom: -9rem;
          right: -7rem;
        }
        .right-to-left {
          bottom: 2rem;
          right: -4rem;
        }
      }
      &:hover {
        .right-to-left {
          transform: rotate(-30deg) scale(1.4) translateY(-1rem) !important;
        }
        .bottom-up {
          transform: scale(1.3) !important;
        }
      }
    }
    .services-card-wrapper {
      height: 23.2rem;
      padding: 2.5rem;
      @include add-all-transition(0.5s, ease-out);
      h3 {
        font-size: 2.3rem;
      }
      p {
        width: 87%;
      }
      .images-wrapper {
        position: absolute;
        width: 100%;
        bottom: 0;
        left: 0;
        .right-to-left {
          @include add-all-transition(0.75s, ease);
        }
        .bottom-up {
          @include add-all-transition(0.75s, ease);
        }
        img {
          @include add-all-transition(1s, ease);
        }
      }
      .learn-more {
        opacity: 0;
        transform: translateX(1rem) translateY(1rem);
        width: 5.5rem;
        border-bottom: 2px solid white;
        @include add-all-transition(0.75s, ease);
      }
      &:hover {
        transform: scale(1.06);
        .learn-more {
          opacity: 1 !important;
          transform: translateX(0) translateY(0) !important;
        }
      }
    }

    .bottom-up,
    .right-to-left {
      -webkit-transition: all 0.3s ease-out;
      -moz-transition: all 0.3s ease-out;
      -ms-transition: all 0.3s ease-out;
      -o-transition: all 0.3s ease-out;
      transition: all 0.3s ease-out;
    }
  }
}

