#homepage-section-4-main-wrapper {
  padding: 4rem 0;
  h4 {
    padding: 0 0 4rem;
  }
  .portfolio-card-transition {
    margin-top: 13rem;
    transition: all 0.75s ease;
    .portfolio-img-wrapper {
      transition: all 0.75s ease;
    }
  }
  .portfolio-container-wrapper {
    #alkira-case-study-block-wrapper {
      .portfolio-inner-wrapper {
        @include background-img-cover(
          "../../assets/images/fm-case-study/fm-word-cloud-home-portfolio.png"
        );
        background-position: bottom !important;
      }
    }
    #fm-case-study-block-wrapper {
      .portfolio-inner-wrapper {
        @include background-img-cover(
          "../../assets/images/fm-case-study/fm-word-cloud-home-portfolio.png"
        );
        background-position: bottom !important;
      }
    }
    #usis-case-study-block-wrapper {
      .portfolio-inner-wrapper {
        @include background-img-cover(
          "../../assets/images/usis-case-study/usis-word-cloud-home-portfolio.png"
        );
        background-position: bottom !important;
      }
    }
    #pel-case-study-block-wrapper {
      .portfolio-inner-wrapper {
        @include background-img-cover(
          "../../assets/images/fm-case-study/fm-word-cloud-home-portfolio.png"
        );
        background-position: bottom !important;
      }
    }
    .fm-portfolio-bg-top-icon {
      position: absolute;
      top: -2.5rem;
      left: -3.5rem;
      img {
        width: 8rem;
      }
    }
    .fm-portfolio-bg-bottom-icon {
      position: absolute;
      bottom: -2rem;
      right: -1.5rem;
      img {
        width: 6rem;
      }
    }
    .usis-portfolio-bg-top-icon {
      position: absolute;
      top: -4.5rem;
      right: 16rem;
      img {
        width: 5rem;
      }
    }
    .usis-portfolio-bg-bottom-left-icon {
      position: absolute;
      bottom: -2rem;
      left: -3.5rem;
      img {
        width: 3rem;
      }
    }
    .usis-portfolio-bg-bottom-icon {
      position: absolute;
      bottom: -2.75rem;
      right: 7.5rem;
      img {
        width: 4rem;
      }
    }
    .pel-portfolio-bg-top-icon {
      position: absolute;
      top: -4.5rem;
      left: 17rem;
      img {
        width: 5rem;
      }
    }
    .pel-portfolio-bg-bottom-icon {
      position: absolute;
      right: 0.5rem;
      bottom: -1.75rem;
      img {
        width: 5rem;
      }
    }
    .pel-portfolio-bg-bottom-left-icon {
      position: absolute;
      bottom: -2rem;
      left: 1rem;
      img {
        width: 5rem;
      }
    }
    .alkira-portfolio-bg-top-icon {
      position: absolute;
      top: -4rem;
      left: 11.5rem;
      img {
        width: 6rem;
      }
    }
    .alkira-portfolio-bg-bottom-icon {
      position: absolute;
      bottom: -2.5rem;
      left: 1rem;
      img {
        width: 5rem;
      }
    }
    .alkira-portfolio-bg-bottom-right-icon {
      position: absolute;
      bottom: -3.25rem;
      right: 7rem;
      img {
        width: 4rem;
      }
    }
    .portfolio-inner-wrapper {
      border-radius: 0.5rem;
      padding: 2.5rem 0 0;
      .portfolio-index-right {
        position: absolute;
        top: -7.5rem;
        right: 2rem;
      }
      .portfolio-index-left {
        position: absolute;
        top: -7.5rem;
        left: 2rem;
      }
      .content-wrapper {
        h5 {
          font-size: 1rem;
        }
        .portfolio-logo-align-left {
          position: absolute;
          top: 0;
          left: 1.5rem;
        }
        .portfolio-logo-align-right {
          position: absolute;
          top: 0;
          right: 1.5rem;
        }
      }
    }
    .portfolio-img-wrapper {
      img {
        width: 65%;
        border-radius: 0.2rem;
        padding: 2rem 0 3rem;
      }
    }
  }
  .section-separator {
    hr {
      border-top: 1px solid $x96-shark;
    }
  }
}

#home-technology-section {
  padding: 6rem 0 8rem;
  .technology-stack-wrapper {
    border-radius: 0.5rem;
    .content-wrapper {
      // height: 20rem;
      padding:2.5rem;
    //   @include background-img-cover("../../assets/images/logos-technology.png");
      background-size: 87%;
      transform: scale(0.8);
      opacity: 0;
      @include add-all-transition(1s, ease);
      h4 {
        font-size: 2rem;
        width: 65%;
        line-height: 1.4;
      }
    }
  }
}

#home-menu-main-wrapper {
  @include add-all-transition(0.5s, ease-in-out);
}

.clip-text {
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.clip-text-unset {
  -webkit-background-clip: unset !important;
  -webkit-text-fill-color: unset !important;
  -webkit-box-decoration-break: clone;
}

.translate-content-in {
  transform: scale(1) !important;
  opacity: 1 !important;
}

.circle-banner-figure {
  left: 37rem;
  @include add-all-transition(0.1s, ease);
  bottom: -10rem;
  img {
    width: 20rem;
  }
}

.rectangle-banner-figure {
  right: -7rem;
  @include add-all-transition(0.1s, ease);
}

.rectangle-left-banner-figure {
  bottom: -5rem;
  left: -13rem;
  @include add-all-transition(0.1s, ease);
  img {
    width: 30rem;
  }
}
